import React, { useState } from 'react';
import ReactDOM from 'react-dom/client';
import { FormControl, Navbar, NavbarBrand, NavItem, NavbarHeader, NavDropdown, Nav, Container, Row, Button, Col, Form, Card, InputGroup, Table } from 'react-bootstrap';
import Badge from 'react-bootstrap/Badge';

import ReactCountryFlag from "react-country-flag"
import carbonEodPricingInputs from './../json/carbonEodPricingInputs.json';
import countryTechYearPricesPriorDay from './../json/countryTechYearPricesPriorDay.json';

import { Tooltip } from 'react-tooltip';
import 'react-tooltip/dist/react-tooltip.css'



// Converting alpha2 to alpha3
var listOfIsoCountries = require("i18n-iso-countries");


// Data from JSON or DB
const geoTechList = {}
carbonEodPricingInputs[0].masterCountries.map(masterCountry => {
    const technologies = Object.keys(masterCountry.technologies)
    technologies.forEach((technology, index) => {
        geoTechList[masterCountry.properties.iso3 + "_" + technology] = masterCountry.technologies[technology];
    })
})


var arrayCountries = []
const peggedCountryList = {}
carbonEodPricingInputs[0].masterCountries.map(masterCountry => {
    arrayCountries.push(masterCountry.properties.iso3.toUpperCase())
    masterCountry.peggedCountries.map(peggedCountry => {
        peggedCountryList[peggedCountry.properties.iso3] = peggedCountry.percentage;
        arrayCountries.push(peggedCountry.properties.iso3.toUpperCase());
    })
})


console.log("arrayCountries_start:")
console.log(arrayCountries)


var arrayYears = []

const timeChangeList = {}
const timeChanges = Object.keys(carbonEodPricingInputs[0].timeChange)
timeChanges.forEach((timeChange, index) => {
    timeChangeList[timeChange] = carbonEodPricingInputs[0].timeChange[timeChange].percentage;
    arrayYears.push(timeChange);
})

arrayYears.reverse();


var arraySelectedCountries = arrayCountries
var arrayUnselectedCountries = []


const arrayTechs = [
    "electricity",
    "gas",
    "arabica",
    "robusta",
    "excelsa",
    "liberica"
]

const countryTechYearPrices = [];

var arraySelectedTech = []




// **********************************************
// Retrieve original values from carbonEodPricingInputs.json
// Loop through MasterCountry/retrieve CountryTech price
// Loop through SlaveCountry/retrieve 
// **********************************************
