import React, { useState } from 'react';
import { Navbar, NavbarBrand, NavItem, NavbarHeader, NavDropdown, Nav, Container, Row, Button, Col, Tab, Tabs } from 'react-bootstrap';


import ReactDOM from 'react-dom/client';

// Main CSS
/*import './index.css';*/

// Modules
import GeoTech2 from './components/GeoTech2';
import CountryPeg from './components/CountryPeg';
import Trend from './components/Trend';
import Ldc from './components/Ldc';
import TimeChange from './components/TimeChange';
import VcsTableVariousTech from './components/VcsTableVariousTech';


function IndexOcean(props) {

  // Data coming from child class
  let [geoTechJsonResponse, setGeoTech] = useState(1);

  let [ldcCountry, setldcCountryArray] = useState(110);
  let [nonLdcCountry, setNonLdcCountryArray] = useState(100);

  //let [cblGeo1, setCblGeo1] = useState(3.2);
  //let [cblGeo2, setCblGeo2] = useState(4.85);
  //let [cblGeo, setCblGeo] = useState(6.05);
  //let [cblCoreli, setCblCoreli] = useState(6.05);
  //let [cblGeoCoreli, setCblGeoCoreli] = useState(100);

  let [vcs, setVcsArray] = useState(100);
  let [gs, setGsArray] = useState(110);
  let [organic, setOrganicArray] = useState(135);


  let [jsonResponseTimeChange, setTimeChange] = useState(1);
  let [jsonResponseCountryPeg, setCountryPeg] = useState(1);

  let [hiddenVcs, setNavSelectionVcs] = useState(true);
  let [hiddenGs, setNavSelectionGs] = useState(true);

  let [showElectricity, setElectricity] = useState(false);
  let [showGas, setGas] = useState(false);
  let [showArabica, setArabica] = useState(true);
  let [showRobusta, setRobusta] = useState(true);
  let [showExcelsa, setExcelsa] = useState(true);
  let [showLiberica, setLiberica] = useState(true);



  return (
    <>
      {/*<Row style={{ marginLeft: 0, marginRight: 0 }}>
        <Col className="TickerBar" sm={12} md={12} lg={12} xl={12} xxl={12}>
          News feed...
        </Col>
      </Row>*/}

      <Tabs
        defaultActiveKey="inputs"
        id="fill-tab-example"
        className="mb-3"
        fill
      >
        <Tab eventKey="inputs" title="Inputs & prices" className='tab'>
          <Row style={{ marginLeft: 0, marginRight: 0 }}>
            <Col sm={4} md={3} lg={3} xl={3} xxl={2} style={{ padding: "0.2em" }}>
              <GeoTech2 jsonResponseGeoTech={setGeoTech} />
              <CountryPeg jsonResponseCountryPeg={setCountryPeg} />
            </Col>

            <Col sm={4} md={3} lg={3} xl={2} xxl={2} style={{ padding: "0.2em" }}>
              <Trend inputVcs={setVcsArray} inputGs={setGsArray} inputOrganic={setOrganicArray}/>
              {/*<Ldc inputLdcCountry={setldcCountryArray} inputNonLdcCountry={setNonLdcCountryArray} />*/}
              <TimeChange jsonResponseTimeChange={setTimeChange} />
            </Col>

            {/* Filters and pricing grids */}
            <Col sm={4} md={6} lg={6} xl={7} xxl={8} style={{ padding: "0.2em" }}>
              <VcsTableVariousTech
                trend="organic"
                geoTech={geoTechJsonResponse}
                jsonResponseCountryPeg={jsonResponseCountryPeg}
                organic={organic}
                jsonResponseTimeChange={jsonResponseTimeChange}
                nonLdcCountryArray={nonLdcCountry}
                ldcCountryArray={ldcCountry}
                showElectricity={showElectricity}
                showGas={showGas}
                showArabica={showArabica}
                showRobusta={showRobusta}
                showExcelsa={showExcelsa}
                showLiberica={showLiberica}
              />
            </Col>
          </Row>

        </Tab>
        <Tab eventKey="prices" title="prices only">
          <Row style={{ marginLeft: 0, marginRight: 0 }}>

            {/* Filters and pricing grids */}
            <Col sm={12} md={12} lg={12} xl={12} xxl={12} style={{ padding: "0.2em" }}>
              <VcsTableVariousTech
                trend="organic"
                geoTech={geoTechJsonResponse}
                jsonResponseCountryPeg={jsonResponseCountryPeg}
                organic={organic}
                jsonResponseTimeChange={jsonResponseTimeChange}
                nonLdcCountryArray={nonLdcCountry}
                ldcCountryArray={ldcCountry}
                showElectricity={showElectricity}
                showGas={showGas}
                showArabica={showArabica}
                showRobusta={showRobusta}
                showExcelsa={showExcelsa}
                showLiberica={showLiberica}
              />
            </Col>
          </Row>

        </Tab>
        <Tab eventKey="charts" title="Charts only">
          Coming soon...
        </Tab>

      </Tabs>


    </>
  );

}


export default IndexOcean;